.App {
  text-align: center;
  /*background: #999;*/
  cursor: pointer;
}
.drap {
  width: 100%;
  height: 500px;
  padding: 20px;
  border: 1px solid red;
  background: #fff;
  margin-top: 20px;
}
.drap-item {
  width: 200px;
  height: 40px;
  background: #888;
  margin: 10px 0;
  line-height: 40px;
  text-align: center;
}
.drap-container {
  width: 400px;
  height: 400px;
  border: 1px solid #555;
}
.is-draging {
  border: 2px solid red;
}
.before {
  position: absolute;
  top: 0;
  background: red;
  height: 2px;
  width: 110%;
  left: -5%;
  z-index: 2;
}
.after {
  position: absolute;
  bottom: 0;
  background: blue;
  height: 2px;
  width: 110%;
  left: -5%;
  z-index: 2;
}
.before:before,
.after:before {
  content: "";
  position: absolute;
  background: red;
  height: 8px;
  width: 8px;
  border-radius: 4px;
  left: -4px;
  top: -3px;
}
.before:after,
.after:after {
  content: "";
  position: absolute;
  background: red;
  height: 8px;
  width: 8px;
  border-radius: 4px;
  right: -4px;
  top: -3px;
}
ul {
  list-style: none;
  width: 300px;
  margin: 40px auto;
  padding: 0;
}
li {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 30px;
  position: relative;
  padding: 5px;
  /*margin: 8px 0;*/
  background: #eee;
}



